import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ModalRoot from '@vkontakte/vkui/dist/components/ModalRoot/ModalRoot';
import ModalPage from '@vkontakte/vkui/dist/components/ModalPage/ModalPage';
import FormLayout from '@vkontakte/vkui/dist/components/FormLayout/FormLayout';
import FormLayoutGroup from '@vkontakte/vkui/dist/components/FormLayoutGroup/FormLayoutGroup';
import Input from '@vkontakte/vkui/dist/components/Input/Input';
import ModalPageHeader from '@vkontakte/vkui/dist/components/ModalPageHeader/ModalPageHeader';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import axios from 'axios';
import Home from './panels/Home';
import Persik from './panels/Persik';
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import Cell from "@vkontakte/vkui/dist/components/Cell/Cell";
import Div from "@vkontakte/vkui/dist/components/Div/Div";

let mutex = {id: 111612348,
first_name: "Филипп Джанковски",
last_name: "",
country: {id: 1, title: "Россия"},
photo_100: "https://sun9-18.userapi.com/c844416/v844416349/1c3076/uKkyiwCBRSE.jpg?ava=1",
photo_max_orig: "https://sun9-45.userapi.com/c844416/v844416349/1c3072/M8g-RdsImqI.jpg?ava=1",
bdate: "28.3.1996",
photo_200: "https://sun9-21.userapi.com/c844416/v844416349/1c3075/7jf2qRe2UaE.jpg?ava=1",
timezone: 7}

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [activeModalS, setActiveModal] = useState(null);
	const [fetchedUser, setUser] = useState(mutex);
	//console.log(fetchedUser);
	const [popout, setPopout] = useState(/*<ScreenSpinner size='large' />*/);


	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_dark';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});

		async function fetchData() {
			let user = await connect.sendPromise('VKWebAppGetUserInfo');
			setUser(user);

			let dataf = await axios.get("https://das.ict.nsc.ru:12000/reg/" + user.id + ':0:secret').then((res) => {
				if (res.data.status == 'ok'){
					user.data = res.data.response;
				} else {
					setActiveModal('register');
					console.log('ERROR!');
				}
			});
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const modal = (
		<ModalRoot activeModal={activeModalS}>
			<ModalPage
				id={'register'}
				header={
					<ModalPageHeader>
						Регистрация
					</ModalPageHeader>
				}
			>
				<FormLayout>

					<FormLayoutGroup top="Введите данные">
						<Input placeholder="Ваш псевдоним" />
						<Input placeholder="Ваш пароль" />
					</FormLayoutGroup>

					<Button size="xl" level="2" onClick={() => setActiveModal(null)}>
						Войти
					</Button>

				</FormLayout>
			</ModalPage>
		</ModalRoot>
	);

	return (
		<View activePanel={activePanel} popout={popout} modal={modal}>
			<Home id='home' fetchedUser={fetchedUser} go={go} setActiveModal={setActiveModal} />
			<Persik id='persik' go={go} />
		</View>
	);

}

export default App;

